.period_container {
    padding: 1rem;
    justify-content: space-evenly;
    background-color: #fff;
    border: rgba(0,0,0,0.2) 0.5px solid;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    color: #6A6A6A;
    font-size: 1.2rem;
    cursor: pointer;
}

.period_item {
    margin: 0 0.5rem;
}